import request from '@/utils/request'


// 
export function detail(data) {
  return request({
    url: '/api/user/transaction/balance_detail',
    method: 'post',
    data
  })
}

// 
export function list(data) {
    return request({
        url: '/api/user/transaction/list',
        method: 'post',
        data
    })
}